/* eslint-disable react/jsx-pascal-case */
import React from "react";
import style from "../css/Payment.module.css";
import { Payment_item } from "../../components";
import * as Icongi from "react-icons/gi";
import * as Iconri from "react-icons/ri";
import * as Iconfa from "react-icons/fa";
import { Redirect } from "react-router-dom";

function PayemntPage() {
  return (
    <div className={style.Wrapper} id="Pricing">
      <div className={style.back}> </div>{" "}
      <ul className={style.ul}>
        <li className={style.li}>
          <Payment_item icon={<Icongi.GiPaperPlane />} />{" "}
        </li>{" "}
        <li className={style.li}>
          <Payment_item icon={<Iconri.RiPlaneFill />} />{" "}
        </li>{" "}
        <li
          className={style.li}
          onClick={() => {
            <Redirect to="/" />;
          }}
        >
          <Payment_item icon={<Iconfa.FaRocket />} />{" "}
        </li>{" "}
      </ul>{" "}
    </div>
  );
}

export default PayemntPage;
